import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';

@Component({
  selector: 'app-decrement-band',
  templateUrl: './decrement-band.component.html',
  styleUrls: ['./decrement-band.component.css']
})
export class DecrementBandComponent implements ICellRendererAngularComp, AfterViewInit {

  params: any;
  data;
  decrementCount;

  constructor() { }

  agInit(params: any): void {
    this.data = params.data;
    this.params = params;
  }

  ngAfterViewInit() {
    const input = document.getElementById('decrement-input');
    input.addEventListener('keyup', (event) => {
      if (event.keyCode === 13) {
        this.decrementBand();
      }
    });
  }

  refresh(params: any): boolean {
    return true;
  }

  decrementBand() {
    this.params.data.sold = this.params.data.sold + this.decrementCount;
    this.params.data.quantity = this.params.data.purchased - this.params.data.sold - this.params.data.wastage;
    this.params.data.soldBands = this.params.data.sold / this.params.data.rollSize;
    this.params.data.quantityBands = this.params.data.quantity / this.params.data.rollSize;
    const quantity = Math.floor((this.params.data.quantityBands - Math.floor(this.params.data.quantityBands)) * this.params.data.rollSize);
    if (quantity <= 10) {
      this.params.data.wastage = this.params.data.wastage + quantity;
      this.params.data.quantity = this.params.data.purchased - this.params.data.sold - this.params.data.wastage;
      this.params.data.quantityBands = this.params.data.quantity / this.params.data.rollSize;
    }
    this.params.api.refreshView();
  }

  disable() {
    if (this.decrementCount !== undefined && this.decrementCount !== '') {
      return false;
    }
    return true;
  }

}
