import { Component, OnInit } from '@angular/core';
import { StockHomeService } from '../../services/stock-home.service';

@Component({
  selector: 'app-stock-history',
  templateUrl: './stock-history.component.html',
  styleUrls: ['./stock-history.component.css']
})
export class StockHistoryComponent implements OnInit {

  columnDefs;
  defaultColDef;
  rowData: any;
  rowDataCopy: any;
  gridApi: any;
  gridColumnApi: any;
  bandNames = [];
  bandName;
  context;

  constructor(
    private stockHomeService: StockHomeService
  ) {
    this.columnDefs = [
      {
        field: 'comfortData.name',
        headerName: 'Name',
        width: 100,
        filter: 'agTextColumnFilter',
        filterParams: {
          filterOptions: ['contains'],
          debounceMs: 0,
          caseSensitive: false,
          suppressAndOrCondition: true
        }
      },
      {
        field: 'comfortData.bandSize',
        headerName: 'Band Size',
        width: 100,
        filter: 'agTextColumnFilter',
        filterParams: {
          filterOptions: ['contains'],
          debounceMs: 0,
          caseSensitive: false,
          suppressAndOrCondition: true
        }
      },
      {
        field: 'quantity',
        headerName: 'Quantity',
        width: 100,
        filter: 'agTextColumnFilter',
        filterParams: {
          filterOptions: ['contains'],
          debounceMs: 0,
          caseSensitive: false,
          suppressAndOrCondition: true
        }
      },
      {
        field: 'bandDescription',
        headerName: 'Description',
        width: 100,
        filter: 'agTextColumnFilter',
        filterParams: {
          filterOptions: ['contains'],
          debounceMs: 0,
          caseSensitive: false,
          suppressAndOrCondition: true
        }
      },
      {
        field: 'dateCreated',
        headerName: 'Date',
        width: 100,
        filter: 'agTextColumnFilter',
        filterParams: {
          filterOptions: ['contains'],
          debounceMs: 0,
          caseSensitive: false,
          suppressAndOrCondition: true
        }
      }
    ];
    this.defaultColDef = {
      resizable: true,
      sortable: true
    };
  }

  ngOnInit() {
    this.stockHomeService.loadingIcon$.next(true);
    this.stockHomeService.getDistinctbands().subscribe(res => {
      this.stockHomeService.loadingIcon$.next(false);
      this.bandNames = res;
    });
  }

  searchHistory() {
    this.stockHomeService.loadingIcon$.next(true);
    this.stockHomeService.getHistory().subscribe(res => {
      this.stockHomeService.loadingIcon$.next(false);
      this.rowData = res;
      this.rowDataCopy = JSON.parse(JSON.stringify(res));
    });
  }

  onGridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    params.api.sizeColumnsToFit();
  }

  getBandHistory(value) {
    this.stockHomeService.loadingIcon$.next(true);
    this.stockHomeService.getHistoryByName(value).subscribe(res => {
      this.stockHomeService.loadingIcon$.next(false);
      this.rowData = res;
      this.rowDataCopy = JSON.parse(JSON.stringify(res));
    });
  }

  setHeight(){
    const height =  window.innerHeight - document.querySelector('.stocks-table').getBoundingClientRect().top;
    return height;
  }
}
